body {
  border-top: 1px solid gray;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  /* background: #eceff1; */
}
body ::-webkit-scrollbar {
  width: 7px;
}

/* Track */
body ::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px lightgray;
}

/* Handle */
body ::-webkit-scrollbar-thumb {
  background: #ccc;
  /*-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); */
}
body ::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0.05);
}

.ant-tabs-bar {
  color: white;
  background: rgba(0, 0, 0, 0.75);
  margin: 0px;
}

.ant-tabs-nav-container {
  font-size: 18px;
}
.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-nav-container {
  height: 35px;
}

.card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab {
  border-color: transparent;
  border-radius: 0;
  background: transparent;
}

.card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active {
  /* border-color: #fff; */
  background: #fff;
}

.ant-tabs-nav .ant-tabs-tab .anticon {
  margin: 0;
  vertical-align: 0;
}

.ant-avatar > img {
  object-fit: cover;
}
.ant-avatar {
  /* avatar is always clickable? NO!*/
  /* cursor: pointer; */
}
.ant-avatar.ant-avatar-icon {
  /* background: #87d068; */
}
.ant-input {
  border-radius: 0;
  resize: none;
  color: #525252;
}
.ant-divider-horizontal.ant-divider-with-text::before,
.ant-divider-horizontal.ant-divider-with-text::after {
  border-top: 1px solid lightgray;
}
.sp-special-tab {
  /* covers entire tab except top bar */
  background: white;
  z-index: 10;
  position: fixed;
  top: 35px;
  width: 100%;
  height: calc(100% - 35px);
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}
.sp-tab-body {
  /* covers entire tab except top bar and header*/
  background: white;
  z-index: 9; /* other profile has z index 10 */
  position: fixed;
  top: 70px;
  width: 100%;
  height: calc(100% - 70px);
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.sp-back-btn {
  position: fixed;
  margin-top: 1px;
  margin-left: 5px;
  background: none;
  border: none;
  font-size: large;
}
.sp-back-btn:hover {
  background: none;
}

.sp-tab-header {
  border-bottom: 1px solid lightgray;
  padding: 5px;
  height: 35px;
}

.sp-toggle-online {
  float: left;
  display: none;
}
.sp-toggle-page-site-chat {
  z-index: 0;
  position: relative;
}

.sp-online-user {
  display: inline-block;
  margin: 5px;
  cursor: pointer;
}
.sp-online-user-username {
  font-size: 10px;
  max-width: 45px;
  /* text-overflow: ellipsis; */
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}

.sp-message-body {
  max-width: calc(100% - 50px);
  margin-top: 5px;
  display: inline-block;
}

.sp-message-body.text {
  background: white;
  border-radius: 5px;
  padding: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  word-wrap: break-word;
  white-space: pre-wrap;
  border: 1px solid #e0e0e0;
  color: #525252;
}

.self .sp-message-body.text {
  text-align: left;
  background: greenyellow;
}

.sp-message-body img {
  max-width: 100px;
}

.sp-chat-message-avatar {
  cursor: pointer;
}

.sp-chat-bottom {
  position: fixed;
  bottom: 0;
  width: 100%;
}

.sp-input-with-picker .ant-input-group-addon {
  border-radius: unset;
}
.sp-input-with-picker .ant-input-group-addon:last-child {
  border-right: 0;
}
.sp-input-with-picker .ant-input-group-addon:first-child {
  border-left: 0;
}
.sp-input-with-picker .ant-input {
  border-right: none;
}

.emoji-mart-preview {
  display: none;
}
.emoji-mart {
  max-width: 100%;
  position: fixed;
  bottom: 39px;
  left: 0px;
  border-bottom: none !important;
  z-index: 10;
}
.emoji-mart .emoji-mart-scroll {
  height: 200px;
}
.emoji-mart-category-label span {
  margin-bottom: 5px;
}
.emoji-mart-emoji-custom span {
  width: 72px !important;
  height: 72px !important;
}
.emoji-mart-category .emoji-mart-emoji span {
  cursor: pointer;
}
.emoji-mart-anchor {
  cursor: pointer;
}

.sp-comment-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #eceff1;
}

.sp-comment-footer textarea.ant-input {
  border-left: 0;
  border-right: 0;
}

.sp-comment-message-avatar {
  cursor: pointer;
}

.sp-comment-message-username {
  font-size: smaller;
  font-weight: bold;
}

.sp-comment-message-avatar {
  vertical-align: top;
  margin-top: 5px;
  margin-right: 5px;
}

.sp-comment-message-footer {
  margin-top: 10px;
  color: gray;
}

.sp-comment-message-score {
  /* margin-top: -3px; */
  margin-left: 3px;
  color: #1890ff;
}

.sp-comment-message-footer .anticon {
  cursor: pointer;
  margin-bottom: -1px;
  display: table-caption;
}

.sp-comment-message-footer .anticon:hover {
  color: black;
}

.sp-comment-message-reply {
  margin-left: 20px;
  cursor: pointer;
}

.sp-comment-message-reply:hover {
  color: black;
}

.sp-comment-message-time {
  font-size: smaller;
  margin-left: 15px;
}

.fileContainer {
  background: none;
  box-shadow: none;
  position: unset;
  border-radius: unset;
  padding: unset;
  display: unset;
  -webkit-align-items: unset;
  align-items: unset;
  -webkit-justify-content: unset;
  justify-content: unset;
  -webkit-flex-direction: unset;
  flex-direction: unset;
  margin: unset;
  transition: unset;
}
.fileContainer .uploadPicturesWrapper {
  display: block;
  -webkit-flex-wrap: unset;
  flex-wrap: unset;
  -webkit-justify-content: unset;
  justify-content: unset;
  width: 100px;
}
.fileContainer .chooseFileButton {
  height: 32px;
  padding: 0 15px;
  font-size: 14px;
  border-radius: 4px;
  line-height: 1.499;
  position: relative;
  display: inline-block;
  font-weight: 400;
  margin: 0;
  white-space: nowrap;
  text-align: center;
  background-image: none;
  border: 1px solid transparent;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
  cursor: pointer;
  transition: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  color: rgba(0, 0, 0, 0.65);
  background-color: #fff;
  border-color: #d9d9d9;
}

.fileContainer .chooseFileButton:hover {
  color: #40a9ff;
  background-color: #fff;
  border-color: #40a9ff;
}

.fileContainer .deleteImage {
  /* display: none; */
  background: #d0d0d0;
  font-size: 20px;
  top: -5px;
  right: -15px;
  /* font-weight: bold; */
}
.fileContainer .deleteImage:hover {
  background: #a0a0a0;
}

.fileContainer .uploadPictureContainer {
  width: unset;
  height: unset;
  margin: unset;
  padding: unset;
  background: unset;
  display: unset;
  -webkit-align-items: unset;
  align-items: unset;
  -webkit-justify-content: unset;
  justify-content: unset;
  height: unset;
  box-shadow: unset;
  border: unset;
  position: unset;
}

.fileContainer .uploadPictureContainer img.uploadPicture {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.uploadPicturesWrapper div {
  /* reverse order so last selected one shows up first */
  /* display: flex; */
  /* flex-direction: column-reverse; */
}

.sp-selected-avatar .fileContainer .chooseFileButton {
  display: none;
}

.sp-selected-avatar .fileContainer .errorsContainer {
  display: none;
}

.sp-follow-stats {
  cursor: pointer;
}
.sp-follow-stats:hover {
  color: rgb(0, 153, 255);
  text-decoration: underline;
}

.sp-follow-tab .ant-avatar {
  margin-right: 10px;
}
.sp-follow-row {
  padding: 10px;
  cursor: pointer;
  /* border-bottom: 1px solid #ececec; */
}
.sp-follow-row:hover {
  background: rgb(236, 236, 236);
}

.sp-inbox-row {
  padding-left: 5px;
  padding-right: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; /* vertically align avatar with text */
  margin-top: -1px; /* so that hover background cover border */
}

.sp-inbox-row:hover {
  cursor: pointer;
  background: rgb(236, 236, 236);
}

.sp-inbox-row .sp-row-right {
  padding: 10px;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: calc(100% - 50px);
  border-bottom: 1px solid #ececec;
}

.sp-inbox-row .sp-message-content {
  font-weight: normal;
  font-weight: initial;
  font-size: small;
  color: gray;
  white-space: nowrap;
  overflow: hidden;
  margin-top: 5px;
  text-overflow: ellipsis;
}

.sp-inbox-row .sp-message-time {
  float: right;
  color: gray;
  font-weight: normal;
  font-weight: initial;
  font-size: smaller;
}

.sp-inbox-conversation .sp-conversation-username {
  color: black;
  cursor: pointer;
  padding: 5px;
}
.sp-inbox-conversation .sp-conversation-username:hover {
  color: #40a9ff;
}

